import React from "react";
import ImageOut from "./interview.jpg";
import LoginStyle from "./Login-style";
import { Button, Grid } from "@material-ui/core";
import { Link } from "react-router-dom";
import MediaCard from '../Card/card';
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import { scroller } from "react-scroll";
import {connect} from 'react-redux';

const mapStateToProps = (state) => {
  return {
    question: state.question,
    langName: state.langName
  }
}

const Login = ({ question, langName }) => {
  const classes = LoginStyle();
  const [explore, setExplore] = React.useState(false);

  const handleExplore =()=>{
    setExplore(true);
    scroller.scrollTo(classes.colorh4, {
      duration: 800,
      delay: 0,
      smooth: "easeInOutQuart",
    });
  }


  return (
    <>
      <Grid container direction="row" className={classes.container}>
        <Grid xs={12} sm={12} md={7} item className={classes.titleDiv}>
          <div style={{ textAlign: "start", margin: " 0 0 12% 1%" }}>
            <h1 className={classes.p1}>
              All in one solution <br /><span style={{ color: "#3f51b5" }}>For interview preperation</span>
            </h1>
          </div>
          <h4>
            <span style={{ color: "#3f51b5" }}> Welcome to TnS Learnings !</span> <br />
            <br />
            TnS Learnings platform provides a complete package of questions and
            answers all in one place which helps in interview preperation. It also
            comes with mock tests where you can practice multilple choice
            questions which covers coding snippets.
          </h4>
          <div className={classes.btnCont}>
            <Link to="/dashboard" className={classes.Link}>
              <Button variant="outlined" color="primary" className={classes.btn}>
                Get Started
              </Button>
            </Link>
            <Button variant="outlined" color="primary" onClick={handleExplore}>
              Explore
            </Button>
          </div>
        </Grid>
        <Grid xs={12} sm={12} md={5} className={classes.imageDiv}>
          <img src={ImageOut} alt="TnS" className={classes.logo} />
        </Grid>
      </Grid>

      <span className={classes.colorh4}></span>
      <br /><br />

      {explore &&
        <div style={{ background: " #eef3f6 " }}>
          <br/>
          <h2 className={classes.colorh4}>
            Select language for interview questions
          </h2>
          <MediaCard />

          <Grid>
            <h4 className={classes.colorh4}>
              List of questions for {langName}
            </h4>

            {question.filter((val) => val.lang === langName)
              .map((data, i) => {
                return (
                  <ul style={{ display: "table" }} key={i}>
                    <li value={data.qText} className={classes.color}>
                      {data.qText}
                    </li>
                  </ul>
                )
              })}
          </Grid>
          <div className={classes.btn}>

            <Link to="/dashboard" className={classes.Link}>
              <Button
                endIcon={<ChevronRightIcon />}
                color="primary"
                variant="outlined"
              >
                Get Started
              </Button><br /><br />
            </Link>
          </div>
        </div>
      }





    </>
  );
};

export default connect(mapStateToProps)(Login);